.login-button {
  width: 448px; 
  height: 56px;
  border-radius: 8px;
  line-height: 100%;
  background-color: #444D63;
  color: #FFFFFF;
  font-size: 24px;
  font-weight: 500;
  margin-top: 105px;
  padding: 16px;
  position: relative;
  box-shadow: 3px 6px 9px rgba(255, 255, 255, 0.16);
}

.login-button:hover {
  opacity: 96%;
}

.login-button::before {
  content: ""; 
  position: absolute;
  background-color: rgba(255, 255, 255, 0.08);
  padding: 20% 50%; 
  border-radius: 50%; 
  left: 50%; 
  top: 50%; 
  -webkit-transform: translate(-50%, -50%) scale(0.6); 
          transform: translate(-50%, -50%) scale(0.6);
  opacity: 0; 
  transition: opacity 1s, -webkit-transform 0.8s; 
  transition: transform 0.8s, opacity 1s; 
  transition: transform 0.8s, opacity 1s, -webkit-transform 0.8s; 
}

.login-button:active::before {
  transition: 0s;
  background-color: #444D63;
  opacity: 1; 
  -webkit-transform: translate(-50%, -50%) scale(0); 
          transform: translate(-50%, -50%) scale(0);  
}

